.header {
    grid-area: a;
    background-color: rgba(74, 198, 230, 0.4);
    display: flex;
    padding-left: 15px;
    border-left:2px solid rgba(74, 198, 230, .9);
    border-right:2px solid rgba(74, 198, 230, .9);
}

.header h1 {
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    margin-left: 15px;
    margin-top: 15px;
    
}

.header img {
    width: 80px;
    margin-bottom: 5px;
}


.positionLog {
    position: relative;
    padding-left: 20px;
}
.positionLog div button{
    position: absolute  ;
    top: 2px;
    left: 100px;
}

