body {
    padding: 0;
    margin: 0;
}
.app-wrapper {
    width: 1400px;
    margin:auto;
    display: grid;

    grid-template-areas: 
    "a a " 
    "b d ";

    grid-template-rows: 60px 1fr;
    
    grid-template-columns: 2fr   10fr ;
    grid-gap: 10px;
}


.app-wrapper-content {
    width: 100%;
    font-family: 'Recursive', sans-serif;

    background-color:rgba(95, 196, 221, 0.1);
    margin-bottom: 100px;
}