.background {
    width: 100%;
    height: 400px;
}








.field-text {
    margin-left: 20px;
}

.field-text h2 {
    font-size: 35px;
    margin-bottom: 5px;
}

.field-text input {
    height: 50px;
    width: 750px;
    outline: none;
    font-size: 30px;
    color: rgba(0, 0, 0, .5);
    border: 1px solid rgba(95, 196, 221, 0.836);
    background-color: rgba(96, 199, 218, 0.438);
    padding-left: 10px;
}

.button {
    width: 150px;
    height: 54.5px;
    cursor: pointer;
    outline: none;
    border: 1px solid rgba(45, 199, 238, 0.9);
    background-color:rgba(45, 199, 238, 0.836);
    position: relative;
    top: -5.8px;
    color: #fff;
    left: 20px;
    font-size: 20px;
    font-family: 'Recursive', sans-serif;
}

.button:hover {
    box-shadow: 0 0 20px rgba(95, 196, 221, 0.836);
}

.Status {
    margin-left: 20px;
}

.targetError .error {
    border:2px solid red;
}