.nav {
    grid-area: b;
    background-color: rgba(72, 192, 176, .7);
    padding-left: 25px; 
    height: 40px;
    transition: .2s;
    font-size: 0;
}


.div_menu {
    position: relative;
    top: -5px;
    font-size: 30px;
    color:rgba(255, 255, 255, 0.904);
}

.nav:hover {
    height: 550px;
    font-size: 20px;
    stroke-opacity: unset;
}

.nav:hover .friends{
    opacity: 1;
}

.nav:hover img{
    opacity: 1;
}

.nav:hover h4{
    font-size: 15px;
}

.nav div {

    padding-bottom: 15px;
}
.nav div a:first-child {
    margin-top: 25px;
}


.nav div a:last-child {
    margin-top: 25px;
}

.nav div a {
    text-decoration: none;
    color: rgb(235, 229, 229);

}

.nav div a.active {
    color: #ffffff;
}

.nav div a:hover {
    color: rgba(236, 230, 230, .6);

}

