.post_tag {
    display: flex;
    margin-bottom: 20px;
}

.post_tag span {
    display: inline-block;
    border-radius: 50%;
    width: 40px;
    height: 40px;
    background-color:rgb(23, 134, 161);
}

.post_tag p {
    font-weight: 550;
    font-size: 17px;
    margin-top: 10px;
    margin-left: 8px;
}

.post_tag img {
    width: 40px;
    height: 40px;
    border-radius:40px;
}

.post_tag button {
    margin-left: 10px;
    
}