

.friends div {
    margin-left: 15px;
    transition: .1s;
    text-align: center;
    font-family: 'Recursive', sans-serif;
    color: rgb(156, 155, 155);
    cursor: pointer;
}
.friends div:first-child {
    margin-left: 0;
}

.friends {
    transition: .1s;
    opacity: 0;
    margin-top: 25px;
    padding-top: 10px;
    height: 60px;
    width: 200px;
    border-radius:7px;
    display: flex;
    justify-content: center;
    background-color:rgba(0, 255, 200, 0.897);
    margin-right: 25px;
}

.friends img{
    opacity: 0;
    width: 40px;
    height: 40px;
    transition: .3s;
    border-radius: 40px;
}

.friends h4 {
    margin: 0;
}